import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { UtilsProvider } from '../../providers/utils/utils';

/**
 * Generated class for the DetalleMovimientoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-detalle-movimiento',
  templateUrl: 'detalle-movimiento.html',
})
export class DetalleMovimientoPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public utils: UtilsProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DetalleMovimientoPage');
  }

  consultaSaldo(){
    this.navCtrl.push("ConsultaSaldosPage");
  }

  productOverView(){
    this.navCtrl.push("ConsultaPage");
  }

  Service(){
    this.navCtrl.push("Tab1Page");
  }

  abrir(){
    this.navCtrl.push("DetalleConsultaPage");
  }

  abrirCuenta(){
    this.navCtrl.push("DetalleCuentaPage");
  }

  movtos(){
    this.navCtrl.push("DetalleConsultaPage");
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }


}



